<template>
	<view class="box">
		<scroll-view scroll-y class="content" @scrolltolower="handleLower">
			<information :name="Name" v-for="(item,index) in collectionList" :key="index" :order="item"
				@onCollection="handleCollection">
			</information>
			<view v-if="collectionList.length === 0" class="nono-list">
				————&nbsp&nbsp&nbsp&nbsp暂无数据&nbsp&nbsp&nbsp&nbsp————
			</view>
		</scroll-view>
	</view>
</template>

<script>
	import Information from '../../activity/components/information/index.vue'
	import {
		PostMyCollectionAjax,
		GetCancelCollectionAjax
	} from '@/apis/api/modules/user-center'
	export default {
		data() {
			return {
				Name: '',
				lower: true,
				keyword: null,
				status: -1,
				pageIndex: 1,
				collectionList: []
			}
		},
		components: {
			Information
		},
		onLoad(e) {
			this.Name = e.Name
		},
		onShow() {
			this.getDate()
		},
		methods: {
			async handleLower() {
				// if (!this.lower) {
				// 	return false
				// }
				this.pageIndex = this.pageIndex + 1

				const result = await PostMyCollectionAjax({
					PageIndex: this.pageIndex,
					PageSize: 5,
					token: true
				})
				if (result.Result.List.length > 0) {
					this.lower = true
					this.collectionList.push(...result.Result.List)
					return false
				}

				this.lower = false
			},
			async getDate() {
				const result = await PostMyCollectionAjax({
					PageIndex: this.pageIndex,
					PageSize: 5,
					token: true
				})
				if (result.Result.List.length === 0) {
					this.lower = true
				}
				// this.lower = false
				this.collectionList = result.Result.List
			},
			/**
			 * 取消收藏
			 */
			async handleCollection(activityId) {
				const result = await GetCancelCollectionAjax({
					CollectionID: activityId,
					token: true
				})
				uni.showToast({
					title: result.Result,
					icon: 'none'
				})
				this.getDate()
			},
		}
	}
</script>

<style scoped lang="scss">
	.box {
		min-height: 100%;
		padding: 30rpx;
		background: #F7F7F7;
		display: flex;
		flex-direction: column;
	}

	.nono-list {
		text-align: center;
		color: #c1c1ba;
		padding: 30rpx 0;
	}

	.content {
		flex: 1;
		overflow: auto;
	}
</style>
